.quotes-container{
    margin-top: 5rem;
    height: 15rem;
    background-color: var(--main-color-green);
    display: flex;
    justify-content: center;
  }

  .quote-container{
    margin: auto 1rem;
    max-width: 20%;
  }

.quote{
    text-align: center;
    font-family: 'Oooh Baby', cursive;
    font-size: 1.7rem;
    color: var(--main-color-grey);
    display: block;
}

.author{
    font-family: 'Oooh Baby', cursive;
    color: var(--main-color-grey);
    text-align: end;
    font-weight: 100;
    font-size: 1.5rem;
    padding-right: 0.5rem;
}

.button-next{
    border: none;
    background-color: transparent;
    cursor: pointer;
    height: 100%;
}

.button-container{
    width: 10rem;
}

.image-next{
    width: 5rem;
    margin: 50%;
}



