.homePage-content {
    display: flex;
    justify-content: center;
    margin: 4rem 0;
}

.home-page-container {
    width: 30rem;
    background-color: var(--main-color-orange--light-text);
    margin: 5% 0;
    padding: 2rem 1.5rem;
    border-radius: 5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: black;
}

.homePage-caption {
    margin: 0 3.5rem;
    text-align: center;
    /* border-bottom: 0.1rem solid; */
    font-size: 2rem;
    color: var(--main-color-brown)
}

.homePage-description {
    text-align: center;
    font-size: 1.1rem;
}