.menu-container {
  position: fixed;
  /* Set the navbar to fixed position */
  top: 0;
  /* Position the navbar at the top of the page */
  width: 100%;
  /* Full width */

  background-color: var(--main-color);
}

.homePage-menuContainer {
  background-color: var(--main-color-orange);
  display: grid;
  grid-template-columns: [first] 15% [second] 70% [third] 15%;
  position: relative;
  box-shadow: 0px 5px 5px grey;
}

.homePage-item--menu {
  display: block;
}

.homePage-menu {
  margin: 0;
  padding: 1rem 3rem;

  list-style: none;

  display: flex;
  grid-column: second;
  justify-content: space-evenly;

}

.homePage-item {
  text-align: center;
}

.homePage-item--logo {
  grid-column: first;
  margin: 0.5rem;
}

.homePage-image:hover{
  cursor: pointer;
}

.homePage-image {
  width: 100%;

}

.homePage-item-button {
  border: none;
  border-radius: 0.5rem;
  padding: 0.5rem;
  text-transform: capitalize;
  background: none;
  font-size: 1.2rem;
}

.homePage-item-button:hover {
  background-color: var(--main-color-brown);
  color: azure;
  transition: background-color 0.3s linear;
  cursor: pointer;
}

.homePage-item-button--dark {
  border: none;
  border-radius: 0.5rem;
  padding: 0.5rem;
  text-transform: capitalize;
  font-size: 1.2rem;
  color: azure;
  background-color: var(--main-color-brown);
}

.homePage-item-button--dark:hover {
  cursor: pointer;
}

.homePage-item--dot {
  height: 10rem;
  width: 10rem;
  margin-top: -2rem;
  margin-left: 0;
  padding: 0.5rem;
  background-color: rgba(240, 182, 56);

  border-radius: 50%;
  overflow: auto;
  position: absolute;

  /* Use absolute positioning */

}

.footer{
  background-color: var(--main-color-brown);
  color: azure;
  margin-top: 3rem;
  background-image: url('./images/logo.png');
  background-repeat: no-repeat;
  background-size: 15%;
  background-position-x: 30%;
  background-position-y: center;
}

.footer-container{
  display: flex;
  flex-direction: column;
  justify-items: center;
  margin: 0 auto;
  font-size: 1.5rem;
  text-transform: uppercase;
  text-align: center;
  padding-top: 0.5rem;
 
}

.footer-list{
  font-size: 1rem;
  text-transform: none;
  list-style: none;
  text-shadow: none;
}

.footer-item{
  padding: 0.3rem;
}

.orange{
  color: var(--main-color-orange);
  font-weight: unset;
}

a{
  color: azure;
}