.prices{
    padding-top: 10rem; 
    color: var(--main-color-brown)
}

.services-list, .check-list-01, .check-list-02, .check-list-03{
    list-style: none;
    margin: 0;
    padding: 3rem;
}

.check-img{
    max-width: 2rem;
}

.prices-box{
    display: grid;
    width: 70%;
    margin: auto;
    grid-template-areas: "a b c d";
    /* border: var(--main-color-brown) solid; */
    border-radius: 5rem;
}

.services-item, .check-item{
    /* min-height: 2.5rem;
    max-height: 2.5rem; */
    height: 3rem;
    margin-bottom: 1rem;
}

.services-item{
    overflow: auto;
}

.services-item--first, .check-item--first, .services-item--last, .check-item--last{
    font-weight: bolder;
    color: var(--main-color-green);
}

.check-list-01, .check-list-02, .check-list-03{
    text-align: center;
}

.services-list{
    text-align: end;
    border-radius: 5rem 0 0 5rem;
    background: linear-gradient(90deg, white, var(--main-color-orange--lighter));
}

.services-item--last, .check-item--last{
    margin-bottom: 0;
}

.check-list-01{
    background-color: var(--main-color-orange--lighter);
}

.check-list-02{
    background-color: var(--main-color-orange--light-bg);
}

.check-list-03{
    background-color: var(--main-color-orange);
    border-radius: 0 5rem 5rem 0;
}

.prices-ps{
    width: 70%;
    margin: auto;
    text-align: start;
    padding: 0.2rem;
}

.assistantPrice-box{
    display: grid;
    grid-template-areas: "a"
                         "a";
    width: 70%;
    margin: auto;
    margin-top: 1rem;
    text-align: center;
    background: linear-gradient(90deg, white, var(--main-color-orange));
    border-radius: 5rem;
    padding: 3rem 0;
}

.assistantPrice-item{
    height: 3rem;
}